  getProducts(){
    return this.products;
  }
  getGriferia(){
    return this.products.filter( r => r.catalogo == 'Griferia');
  }
  getCerradura(){
    return this.products.filter( r => r.catalogo == 'Cerradura');
  }

  getCerraduraProductos(){
    return this.products.filter( r => r.category == 'Product' && r.catalogo == 'Cerradura');
  }

  getCerraduraAccesorios(){
    return this.products.filter( r => r.category == 'Accesory' && r.catalogo == 'Cerradura');
  }

  getProduct(isCerradura:any,index: number){
    if (isCerradura) {
      this.filteredproducts = this.products.filter(r => r.catalogo == 'Cerradura')
    } else{
      this.filteredproducts = this.products.filter(r => r.catalogo == 'Griferia')
    }
    console.log(this.filteredproducts);
    console.log(index);
    return this.filteredproducts[index];
  }

  getCatalogo(product: any){
    return product.catalogo;
  }
  
  updateSelectedProduct(product: Object){
    this.selectedproduct = product;
  }
